
//
export function priceHander(price,type) {
		if (type === 1) {
			return Math.floor(price).toString();
		} else if (type === 2) {
				const decimalPart = price - Math.floor(price);
				return decimalPart == 0 ? '' : decimalPart.toFixed(1).slice(1);
		} 
}

