//
import { debounce } from "@/common/throttle.js"; //
export function preservationImg(n,img) {
	uni.authorize({
		scope: 'scope.writePhotosAlbum',
		/* scope.writePhotosAlbum  */
		success() {
			/*  */
			if(n){
				imgArrApi(img); /*  */
			}else{
				imgApi(img)
			}
		},
		complete(res) {
			/*  */
			uni.getSetting({
				success(res) {
					if (!res.authSetting['scope.writePhotosAlbum']) {
						openInstall(); /*  */
					}
				}
			});
		}
	});
}

function imgArrApi(images) { //
	uni.showLoading({
		title: ''
	});
	
	/*  */
	images.forEach((res,index)=>{
		uni.getImageInfo({
			src: res,
			success: function(image) {
				/*  */
				uni.saveImageToPhotosAlbum({
					filePath: image.path,
					success: function() {
						if(index == images.length-1){
							uni.hideLoading();
							uni.showToast({ title: '', icon:'none' })
						}
					},
					fail(res) {
						console.log('arr',res);
						uni.showToast({
							title: ''
						});
						uni.hideLoading();
					}
				});
			}
		});
	})
}

function imgApi(image) { //
		uni.getImageInfo({
			src: image,
			success: function(image) {
				/*  */
				uni.saveImageToPhotosAlbum({
					filePath: image.path,
					success: function() {
						uni.showToast({ title: '', icon:'none' })
					},
					fail(res) {
						console.log('',res);
						uni.showToast({
							title: ''
						});
					}
				});
			}
		});
}

function openInstall() {
	uni.showModal({
		content: ',',
		success: function(res) {
			if (res.confirm) {
				uni.openSetting({
					/* API*/
					success(res) {
						console.log(res.authSetting);
					}
				});
			} else if (res.cancel) {
				uni.showModal({
					cancelText: '',
					confirmText: '',
					content: '',
					success: function(res) {
						if (res.confirm) {
							uni.openSetting({
								success(res) {
									console.log(res.authSetting); /*  */
								}
							});
						} else if (res.cancel) {
							console.log('');
						}
					}
				});
			}
		}
	});
}